.navbar{
    width: 100%;

    &_top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 10rem;
        background-color: $color-primary;

        &_logo{
            margin-left: 4rem;

            &_link{
                font-size: 2.8rem;
                color: $color-white;
                text-decoration: none;
            }
        }
        &_auth{
            margin-right: 4rem;

            &_link{
                font-size: 1.8rem;
                color: $color-white;
                text-decoration: none;

                &:first-of-type{
                    margin-right: 8rem;
                }
            }
        }
    }

    &_bottom{
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        height: 7rem;
        background-color: $color-tertiary;

        &_item{
            list-style: none;

            &_link{
                font-size: 1.8rem;
                color: $color-white;
                text-decoration: none;

                &:hover{
                    color: $color-quaternary;
                }
            }
        }
    }
}